import React, { Fragment } from "react";
import { Modal } from "react-bootstrap";

import { useForm } from "react-hook-form";
import { useMutation } from "@apollo/react-hooks";
import UPDATE_PRODUCT from "../../graphql/UpdateProduct";

import "../other/Checkout.css";

function ProductModal(props) {
  const { refetch } = props;

  const [updateProducts, { loading, error }] = useMutation(
    UPDATE_PRODUCT,
    {
      onCompleted() {
        refetch();
      },
    }
  );

  const { handleSubmit, register, errors } = useForm();
  const { productId } = props;
  const { onHide } = props;
  const { productData } = props;

  const onSubmit = (data) => {
   const vars = data
    updateProducts({
      variables: {
        id: productId,
        vars
      },
    });
    onHide();
  };

  const product =
    productData &&
    productId &&
    productData.products.filter((product) => product.id === productId)[0];

  return (
    <Fragment>
      <Modal
        show={props.show}
        onHide={props.onHide}
        className='product-quickview-modal-wrapper'
      >
        <Modal.Header closeButton><h4>rn Bilgilerini Gncelle</h4></Modal.Header>

        <div className='billing-info-wrap m-4'>
          <div className='row'>
            <div className='col-lg-6 col-md-6'>
            </div>
            <div className='col-lg-6 col-md-6'></div>
          </div>
          <form onSubmit={handleSubmit(onSubmit)}>
            {product && (
              <div className='row'>
                <div className='col-lg-12'>
                  <div className='billing-info mb-20'>
                    <label>sim</label>
                    <input
                      maxLength='55'
                      className='billing-address'
                      placeholder='ller'
                      type='text'
                      ref={register({ required: true, minLength: 2 })}
                      name='name'
                      defaultValue={product.name}
                    />
                    {errors.name && (
                      <div className='alert alert-danger small' role='alert'>
                        sim bo braklamaz.
                      </div>
                    )}
                  </div>
                  <div className='billing-info mb-20'>
                    <label>Zemin Rengi</label>
                    <input
                      maxLength='55'
                      className='billing-address'
                      placeholder='Zemin Rengi'
                      type='text'
                      ref={register({ required: true, minLength: 2 })}
                      name='base_color'
                      defaultValue={product.base_color}
                    />
                    {errors.base_color && (
                      <div className='alert alert-danger small' role='alert'>
                        Zemin Rengi bo braklamaz.
                      </div>
                    )}
                  </div>
                  <div className='billing-info mb-20'>
                    <label>ller</label>
                    <input
                      maxLength='55'
                      className='billing-address'
                      placeholder='ller'
                      type='text'
                      ref={register({ required: true, minLength: 2 })}
                      name='dimensions'
                      defaultValue={product.dimensions}
                    />
                    {errors.dimensions && (
                      <div className='alert alert-danger small' role='alert'>
                        ller bo braklamaz.
                      </div>
                    )}
                  </div>
                  <div className='billing-info mb-20'>
                    <label>Detayl Bilgi</label>
                    <textarea
                      maxLength='1255'
                      className='billing-address'
                      placeholder='Detayl Bilgi'
                      rows='3'
                      ref={register({ required: true, minLength: 7 })}
                      name='fullDescription'
                      defaultValue={product.fullDescription}
                    />
                    {errors.fullDescription && (
                      <div className='alert alert-danger small' role='alert'>
                        Detayl bilgi bo braklamaz.
                      </div>
                    )}
                  </div>
                  <div className='billing-info mb-20'>
                    <label>Ksa Aklama</label>
                    <input
                      maxLength='755'
                      className='billing-address'
                      placeholder='Ksa aklama'
                      type='text'
                      ref={register({ required: true, minLength: 2 })}
                      name='shortDescription'
                      defaultValue={product.shortDescription}
                    />
                    {errors.shortDescription && (
                      <div className='alert alert-danger small' role='alert'>
                        Ksa aklama bo braklamaz.
                      </div>
                    )}
                  </div>
                  <div className='billing-info mb-20'>
                    <label>Arlk</label>
                    <input
                      maxLength='22'
                      className='billing-address'
                      placeholder='Arlk'
                      type='text'
                      ref={register({ required: true, minLength: 2 })}
                      name='weight'
                      defaultValue={product.weight}
                    />
                    {errors.weight && (
                      <div className='alert alert-danger small' role='alert'>
                        Arlk bo braklamaz.
                      </div>
                    )}
                  </div>
                  <div className='billing-info mb-20'>
                    <label>ndirim Oran</label>
                    <input
                      maxLength='3'
                      className='billing-address'
                      placeholder='ndirim oran'
                      type='number'
                      ref={register({ required: true, minLength: 1 })}
                      name='discount'
                      defaultValue={product.discount}
                    />
                    {errors.discount && (
                      <div className='alert alert-danger small' role='alert'>
                        ndirim oran bo braklamaz.
                      </div>
                    )}
                  </div>
                  <div className='billing-info mb-20'>
                    <label>Fiyat</label>
                    <input
                      maxLength='4'
                      className='billing-address'
                      placeholder='Fiyat'
                      type='number'
                      ref={register({ required: true, minLength: 1 })}
                      name='price'
                      defaultValue={product.price}
                    />
                    {errors.price && (
                      <div className='alert alert-danger small' role='alert'>
                        Fiyat bo braklamaz.
                      </div>
                    )}
                  </div>
                  <div className='billing-info mb-20'>
                    <label>Rating</label>
                    <input
                      maxLength='4'
                      className='billing-address'
                      placeholder='Rating'
                      type='number'
                      ref={register({ required: true, minLength: 1 })}
                      name='rating'
                      defaultValue={product.rating}
                    />
                    {errors.rating && (
                      <div className='alert alert-danger small' role='alert'>
                        Rating bo braklamaz.
                      </div>
                    )}
                  </div>
                  <div className='d-flex'>
                    <label>Yeni</label>
                    <input
                      className='checkbox'
                      type='checkbox'
                      name='new'
                      defaultValue={true}
                      ref={register}
                    />
                  </div>
                  <div className='d-flex'>
                    <label>Aktif</label>
                    <input
                      className='checkbox'
                      type='checkbox'
                      name='isActive'
                      defaultValue={true}
                      ref={register}
                    />
                  </div>
                </div>
                <div className='button-box pl-15'>
                  <button className='submitAddress' type='submit'>
                    <span>{loading? 'Gncelleniyor...' : 'Gncelle'}</span>
                  </button>
                </div>
                {error&& 'rn gncellenemedi, daha sonra tekrar deneyin.'}
              </div>
            )}
          </form>
        </div>
      </Modal>
    </Fragment>
  );
}

export default ProductModal;
